.modal-container {
    overflow-x: auto;
  
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
    background-color: #ffffff;
    font-family: Roboto, sans-serif;
    font-weight: 500;
    font-size: 12px;
    line-height: calc(16 / 12);
    color: #000000;
    max-height: 100vh;
    width: 100%;
    padding: 48px 20px 40px 20px;

    @media screen and (min-width: 768px) and (max-width: 1023px) {
        display: flex;
        margin: auto;
        width: 100%;
        height: 570px;
        padding: 40px 36px 40px 36px;
    
      }
      @media screen and (min-width: 1024px) {
        display: flex;
        max-width: 882px;
        height: 568px;
        padding: 40px 16px 40px 16px;
      }
      .backdrop.is-hidden & {
        transform: translate(-50%, -50%) scale(0.8);
      }
}

.backdrop{
    z-index: 999;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
    padding: 0 20px;

    @media screen and (min-width: 768px) {
        padding: 0 32px;
      }
}

.image {
    margin-right: auto;
    margin-left: auto;
    display: block;
  
    max-width: 240px;
    height: 356px;
    border-radius: 5px;
    margin-bottom: 30px;
    @media screen and (min-width: 768px) and (max-width: 1023px) {
      max-width: 264px;
      height: 373.55px;
    }
    @media screen and (min-width: 1024px) {
      max-width: 396px;
      height: 477.8px;
    }
  }
  .film__image {
    @media screen and (min-width: 768px) {
      margin-right: 30px;
    }
  }
  .film__information {
    text-align: start;
    @media screen and (min-width: 768px) {
        margin-left: 16px;
      }
  }
  
  .film__title {
    font-size: 20px;
    line-height: 1.15;
    margin-bottom: 20px;
    @media screen and (min-width: 1024px) {
      font-size: 30px;
      line-height: 1.17;
    }
  }
  
  .film__item {
    display: flex;
    margin-bottom: 10px;
  }
  .film__details {
    color: second-text-color;
    min-width: 108px;
  }
  
  .film__info--uper {
    text-transform: uppercase;
    line-height: 1.17;
  }
  
  .film__rating--orange {
    padding-left: 10px;
    padding-right: 10px;
    color: #ffffff;
    background-color: #ff6b08;
    border-radius: 5px;
  }
  .film__rating--divider {
    margin-right: 3px;
  }
  
  .film__about__title {
    text-transform: uppercase;
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .film__about__text {
    line-height: 1.67;
    margin-bottom: 21px;
  }
  .add-btns {
    display: flex;
    margin-bottom: 16px;
    // display: flex;
    // margin-left: 331px;
    // margin-bottom: 323px;
    // margin-top: -75px;
    @media screen and (min-width: 768px) and (max-width: 1023px) {
      margin-left: 265px;
      margin-top: -75px;
    }
    @media screen and (min-width: 1024px) {
      display: flex;
    margin-left: 331px;
    margin-bottom: 323px;
    margin-top: -75px;
    }
  }
  .modal-btn {
    width: 112px;
    height: 44px;
    text-align: center;
    text-transform: uppercase;
    border-radius: 5px;
    background-color: #ffffff;
    cursor: pointer;
    border: 1px solid #000000;
    transition: all 300ms cubic-bezier(0.38, 0.53, 0.8, 0.71);
    &:hover,
    &:focus {
      background-color: #ff6b08;
      color: #ffffff;
      border: 1px solid #ff6b08;
    }
    @media screen and (min-width: 768px) and (max-width: 1023px) {
      width: 125px;
    }
    @media screen and (min-width: 1024px) {
      width: 136px;
    }
  }
  
  .modal-btn.active {
    background-color: #ff6b08;
    color: #ffffff;
    border: 1px solid #ff6b08;
  }
  
  .modal-btn:first-child {
    margin-right: 15px;
  }

  .info-wraper{
    @media screen and (min-width: 768px) {
        display: flex;
      }
  }

  .modal-close-btn {
    position: absolute;
    width: 25px;
    height: 25px;
    top: 8px;
    right: 10px;
    display: inline-flex;
    align-self: flex-start;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    border: 1px solid rgba(0,0,0,.8);
    box-sizing: border-box;
    cursor: pointer;
    transition: color .3s cubic-bezier(.38,.53,.8,.71);
    fill: #000;
    background-color: #f7f7f7;
}

.is-hidden{
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}